@import url(https://fonts.googleapis.com/css?family=Montserrat|Open+Sans);
body{
    background-color: #151515;
    overflow-y: scroll;

}
.button{
    position: fixed;
    top: 70vh;
    width: 180px;
    left: calc( 50vw - 90px ) ;
}
.logo-clear{
    position: fixed;
    top: 20vh;
    height: 20vh;
    width: 20vh;
    left: calc( 50vw - 10vh );
    background-image: url(/FindC/static/media/logo-clear.703c1ed2.svg);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}
.name{
    font-family: 'Montserrat', sans-serif;
    position: fixed;
    top: 43vh;
    font-size: 10vh;
    width: 100vw;
    text-align: center;
    color: white;
}
.container-about {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 10vh;
    box-sizing: border-box;
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
}
.intro{
    margin-top: 5vh;
    font-size: 40px;    
    font-weight: bold;
    color: white;
}
.section{
    font-size: 18px;
    color: white;
    box-sizing: border-box;
    padding: 15px;
    height: auto;
    font-family: 'Montserrat';
}
.section span{
    font-family: serif;
    font-family: initial;
    color: #A27DFF;
    font-size: 24px;
    text-align: center;
    font-weight: bold;
}
@media (min-width: 768px) {
    .container-about {
        width: 750px;
    }
}
@media (min-width: 992px) {
    .intro{
        font-size: 100px;    
        grid-column: 1/4;
    }
    .container-about {
        grid-template-columns: repeat(3, 1fr);
        width: 970px;
    }
}
@media (min-width: 1200px) {
    .container-about {
        width: 1170px;
    }
}
